import './foot.scss'

import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Badge from 'vux/src/components/badge/index.vue'


import { fetch, rap } from 'js/fetch.js'
import bus from 'js/bus.js'

import cookie from 'vux/src/tools/cookie/index.js'

import utils from 'js/utils.js'

let url = {
info: '/user/getUser.do',
logout: '/user/logout.do',
stationLetter: 'api/messages/unread-count'
}
url = rap(url)

export default {
props: {
index: {
default: 1
}
},
data() {
return {
mobile: '',
isLogin: false,
lerrerNumber: '0',
token: '',
timer: 10000
}
},
created() {
let _this = this;

this.isLogin = utils.isLogin()

if (this.isLogin) {
this.token = cookie.get('token')
this.stationLetter()

setInterval(() => {
this.stationLetter()
}, this.timer)

}
},
methods: {
stationLetter() {
fetch('get', `${url.stationLetter}`, {}, { 'headers': this.token }).then(res => {
if (res.status >= 200 && res.status <= 300) {

this.lerrerNumber = '' + res.data.unread_count
window.localStorage.setItem('lerrerNumber',this.lerrerNumber)

} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})
},
golink(e) {

if (e.target.className.indexOf('vux-badge') != -1) {
window.location.href = './message_list.html'
return
}

if (this.isLogin) {

window.location.href = './member.html'
} else {

window.location.href = './login.html'
}
}
},
components: {
Tabbar,
TabbarItem,
Badge
}
}



