import axios from 'axios'

import qs from 'qs'

import cookie from 'vux/src/tools/cookie/index.js'



let host = require('./host-config.js').host


function rap(urlList) {
let obj = {}
Object.keys(urlList).forEach(key => {
obj[key] = host + urlList[key]
})
return obj
}

axios.defaults.validateStatus = function (status) {
return status >= 200 && status <= 500; 
}

function fetch(type = 'get', url, data, config) {
return new Promise((resolve, reject) => {



if (typeof (config) == 'undefined') {

} else {
axios.defaults.headers.common['Accept'] = 'application/json';
axios.defaults.headers.common['Authorization'] = config.headers;
}




var loadinginstace








axios.interceptors.response.use(data => {// 响应成功关闭loading

if(data.status=='401'){

cookie.remove('token')
window.location.href='./login.html'
}
return data
}, error => {



return Promise.reject(error)
})
switch (type) {
case 'get':
axios.get(url, { params: data }).then((response) => {
resolve(response)
}).catch((error) => {
console.log(error)
reject({
status: -1,
message: '系统错误，请稍后重试'
})
})
break;
case 'post':
axios.post(url, qs.stringify(data)).then((response) => {
resolve(response)
}).catch((error) => {
alert(error.message)

reject({
status: -1,
message: '系统错误，请稍后重试'
})
})
break;
case 'delete':

axios.delete(url, { params: data }).then((response) => {
resolve(response)
}).catch((error) => {
console.log(error)
reject({
status: -1,
message: '系统错误，请稍后重试'
})
})
break;
default:
}













})
}
export {
rap,
fetch
}



