import 'vux/src/styles/reset.less';
import './books_detail.scss'
import 'vux/src/styles/1px.less';
import utils from 'js/utils.js'

import Swiper from 'vux/src/components/swiper/swiper.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


Vue.use(ConfirmPlugin)


import { hostImg } from 'js/host-config'
import cookie from 'vux/src/tools/cookie/index.js'


import debounce from 'vux/src/tools/debounce/index.js'


import { fetch, rap } from 'js/fetch.js'
let url = {
getBookDetail: 'api/books',//获取图书详情
getCollectCount: 'api/book/collect/count',//收藏统计
postCollectBook: 'api/books'
}
url = rap(url)

import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
transitionName: 'slide-left',
categoriesList: [{ name: '详情' }, { name: '留言(100)' }],
listIndex: 0,
bookID: 0,
bookInfo: {},
isCollect: false,//是否收藏
clock: false

},
created() {
this.isLogin = utils.isLogin()
this.token = cookie.get('token')
this.bookID = utils.getQuery('id')
this.getBookDetail()

},
methods: {
collectButton(e, id) {

if (this.isLogin) {

this.collect(e, id)
} else {

this.promptLogin()
}
},
promptLogin() {

this.$vux.confirm.show({
title: '收藏图书',
content: '收藏之前请先登录',
onConfirm() {
window.location.href = './login.html'
}
})
},
collect: debounce(function (e, id) {

if (this.clock == true) {
return
}

this.clock = true
if (e.target.className.indexOf('icon-shoucang1') != -1) {

fetch('post', `${url.postCollectBook}/${id}/collect`,{},{'headers':this.token}).then(res => {
if (res.status >= 200 && res.status <= 300) {
e.target.className = 'iconfont icon-shoucang2'
this.bookInfo.collect_count >> 0
this.bookInfo.collect_count += 1
this.clock = false











} else {
this.clock = false










}
})

} else if (e.target.className.indexOf('icon-shoucang2') != -1) {

fetch('delete', `${url.postCollectBook}/${id}/uncollect`,{},{'headers':this.token}).then(res => {
if (res.status >= 200 && res.status <= 300) {

e.target.className = 'iconfont icon-shoucang1'
this.bookInfo.collect_count >> 0
this.bookInfo.collect_count -= 1
this.clock = false











} else {

this.clock = false










}
})
}
}, 500, {}),
show(index) {
if (this.listIndex > index) {
this.transitionName = 'slide-right'
} else if (this.listIndex > index) {
this.transitionName = 'slide-left'
}
this.listIndex = index
},
getBookDetail() {
fetch('get', `${url.getBookDetail}/${this.bookID}`,{},{'headers':this.token}, {
}).then(res => {
if (res.status >= 200 && res.status <= 500) {
res.data.cover = `${hostImg}${res.data.cover}`
this.bookInfo = res.data

}
})
}
},
components: {
Cell,
Group,
Swiper,
Scroller,
Tab,
TabItem,
Sticky,
Flexbox,
FlexboxItem,
ViewBox,
XButton
},
mixins: [mixin]
})



